import s from "./ProfileInfo.module.css";
import React from "react";
import StatusProfileWithHook from "./StatusProfile/StatusProfileWithHook";
import AvatarProfile from "./AvavatarProfile/AvavatarProfile";

const ProfileInfo = (props) => {
    return (
        <div>
            <img className={s.profile_logo}
                 src="https://www.komandirovka.ru/upload/save_file33/bf5/bf5c6f100d3d620c48787b3714cb4fea.jpg" alt=""/>
            <div className={s.descriptions}>
                <div className={s.descriptions_logo}>
                <AvatarProfile isOwner={props.isOwner} photo={props.profile.photos.large}
                                 addChangeAvatar={props.addChangeAvatar}/>
                </div>
                <div className={s.descriptions_text}>
                    <div className={s.descriptions_name}>
                        {props.profile.fullName}
                    </div>
                    <StatusProfileWithHook isOwner={props.isOwner} status={props.status}
                                           updateUserStatus={props.updateUserStatus}/>
                    <div className={s.descriptions_about}>
                        <p>Ищешь работу: {props.profile.lookingForAJob ? 'да' : 'нет'}</p>
                        {props.profile.lookingForAJobDescription &&
                            <p>Описание работы: {props.profile.lookingForAJobDescription}</p>}
                         <p className={s.contacts}>Контакты</p>
                        {props.profile.contacts.facebook &&
                            <p>Страница в facebook: {props.profile.contacts.facebook}</p>}
                        {props.profile.contacts.website &&
                            <p>Мой веб-сайт: {props.profile.contacts.website}</p>}
                        {props.profile.contacts.vk &&
                            <p>Страница ВКонтакте: {props.profile.contacts.vk}</p>}
                        {props.profile.contacts.twitter &&
                            <p>Страница в twitter: {props.profile.contacts.twitter}</p>}
                        {props.profile.contacts.instagram &&
                            <p>Страница в instagram: {props.profile.contacts.instagram}</p>}
                        {props.profile.contacts.youtube &&
                            <p>Мой канал на youtube: {props.profile.contacts.youtube}</p>}
                        {props.profile.contacts.github &&
                            <p>Мой репозиторий на github: {props.profile.contacts.github}</p>}
                        {props.profile.contacts.mainLink &&
                            <p>Страница в mainLink: {props.profile.contacts.mainLink}</p>}
                    </div>
                </div>
            </div>

        </div>
    )

};

export default ProfileInfo;