import {authAPI, profileAPI, securityAPI, usersAPI} from "../api/api";

const SET_USER_DATA = 'ruslnetwork/authReducer/SET_USER_DATA';
const ADD_USER_PHOTO = 'ruslnetwork/authReducer/ADD_USER_PHOTO';
const ERROR_AUTH = 'ruslnetwork/authReducer/ERROR_AUTH';
const SET_CAPTCHA_URL_SUCCESS = 'ruslnetwork/authReducer/SET_CAPTCHA_URL_SUCCESS';

let initialState = {
    id: null,
    login: null,
    email: null,
    isFetching: false,
    isAuth: false,
    userPhoto: '',
    errorAuth: null,
    captchaURL: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        case ADD_USER_PHOTO:
            return {
                ...state,
                userPhoto: action.userPhoto,
            }
        case ERROR_AUTH:
            return {
                ...state,
                errorAuth: action.message,
            }
        case SET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                captchaURL: action.captchaURL
            }
        default:
            return state;
    }
}


const setAuthUserData = (id, login, email, isAuth) => {
    return {
        type: SET_USER_DATA,
        payload: {id, login, email, isAuth}
    }
}

const addUserPhotoSuccess = (userPhoto) => {
    return {
        type: ADD_USER_PHOTO,
        userPhoto,
    }
}

export const addUserPhoto = () => {
    return async (dispatch) => {
        const response = await profileAPI.getUserProfile(15337)
        dispatch(addUserPhotoSuccess(response.data.photos.small))
    }
}

const errorAuth = (message) => {
    return {
        type: ERROR_AUTH,
        message,
    }
}

const setCaptchaURLSuccess = (captchaURL) => {
    return {
        type: SET_CAPTCHA_URL_SUCCESS,
        captchaURL
    }
}

const setCaptchaURL = () => {
    return async (dispatch) => {
        const response = await securityAPI.captchaURL()
        dispatch(setCaptchaURLSuccess(response.data.url))
    }
}

export const login = (email, password, rememberMe, captcha) => {
    return  async (dispatch) => {
            const response = await authAPI.login(email, password, rememberMe, captcha)
            if (response.data.resultCode === 0) {
                dispatch(getAuthUser())
                dispatch(errorAuth(null))
            } else {
                if(response.data.resultCode === 10) {
                    dispatch(setCaptchaURL())
                }
                dispatch(errorAuth(response.data.messages[0]))
            }
            dispatch(setCaptchaURLSuccess(null))
    }
}

export const logout = () => {
    return (dispatch) => {
        authAPI.logout().then(response => {
            dispatch(setAuthUserData(null, null, null, false))
        })
    }
}

export const getAuthUser = () => {
    return async (dispatch, getState) => {
        const response = await authAPI.me()
        if (response.data.resultCode === 0) {
            dispatch(setAuthUserData(response.data.data.id, response.data.data.login, response.data.data.email, true))
        }
        dispatch(addUserPhoto(getState().auth.id))
    }
}

export default authReducer;
